.contact{
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100vw;
  min-height: 100vh;
  /* background: linear-gradient(-45deg, #f3b8a6, #f99cc0, #ace1f4, #aaefdf); */

  background: linear-gradient(180deg, #202020 0%, rgba(32, 32, 32, 0.2) 50%, #202020 100%), url(https://images.pexels.com/photos/1779487/pexels-photo-1779487.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.contact-title{
  color: #fcf9f9;
  font-size: 2.5rem;
}

.contact-form{
  display: flex;
  flex-direction: column;
  /* padding: 1rem; */
  /* gap: 1rem; */
  border: 1px solid #f6f4f5;
  border-radius:20px;
  /* background: #252525; */
  max-width: 375px;
  width: 100%;
}

label{
  display: flex;
  flex-direction: column;
  gap: .5rem;

}
.input-name{
  color: rgb(7, 7, 7);
}

label input, label textarea{
  background: #f5f3f3;
  padding: .75rem;
  font-size: 1rem;
  border-radius: .5rem;
  color: #070707;
  border: 1px solid transparent;
  transition: all .25s ease 0s;
  box-shadow: 0 0 0 1px #9A1750;


}

label textarea{
  max-width: 100%;
  max-height: 200px;
  min-width: 200px;
  min-height: 100px;
}

.send-btn{
  background: linear-gradient(90deg, #EE4C9C 0%, #9A1750 100%);
  padding: .75rem;
  font-size: 1rem;
  font-weight: 600;
  color: #CCC;
  border-radius: .5rem;
  cursor: pointer;
}

label input:hover,
label textarea:hover,
label input:focus,
label textarea:focus{
  border-color: #f1f0f0;
  box-shadow: 0 0 0 2px #9A1750;
}

.send-btn:hover, .send-btn:focus{
  color: white;
  background: linear-gradient(90deg, #EE4C9C 0%, #9A1750 100%);
}

@media screen and (max-width: 900px){
  .contact{
    flex-direction: column;
    gap: 1rem;
  }
  .contact-title{
    text-align: center;  
  }
}

