.social-links{
  width: 50px;
  height: 55px;
  line-height: 50px;
  color: #fff;
  background: #9A1750;
  border-radius: 5px;   
  transition: .3s ease all;
  
  
}
.social-links i{
  margin: auto;
}


.social-links:nth-child(2):hover{
  background: #0072B1;
}
.social-links:nth-child(3){
  position: relative;
  border-radius: 5px;
  overflow: hidden;
  
}
.social-links:nth-child(3):hover {
  background: transparent;
}
.social-links:nth-child(3)::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  z-index: -1;
  transition: .3s ease all;
  background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
}
.social-links:nth-child(3):hover::before {
  opacity: 1;
}
.social-links:nth-child(4):hover {
  background: #201db9;
}
.social-links:nth-child(5):hover,.social-links:nth-child(1):hover {
  background: #ffffff;
  color: #000;
}


